@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --white: #fff;
  --bg-white:#f8f8f8;
}


.App {
  text-align: center;
  display: flex;
  flex-direction: row;

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media (min-width: 1600px){.sk-body{width:100%}.sk-body header{position:fixed;left:0;top:0;height:100%;width:250px;display:flex;flex-direction:column;justify-content:center;align-items:flex-start;border-right:1px solid #ebebeb;padding:0 100px;z-index:1;padding-bottom:4rem;background:white}.sk-body header strong,.sk-body header a,.sk-body header li{font-family:'Montserrat', sans-serif}.sk-body header strong{font-size:28px;font-weight:900;text-transform:uppercase}.sk-body header li{letter-spacing:2px;height:34px;transform:scale(1.2);font-size:15px;transition:all .3s ease;transform-origin:0% 50%;color:#767676;cursor:pointer}.sk-body header .nav-a:hover{transform:scale(1.3);color:#000}.sk-body header menu{list-style:none;padding:40px 0 90px;text-align:start}.sk-body .right-part{padding-left:450px;position:relative;float:left;width:100%;min-height:100vh;box-sizing:border-box;background-color:#f8f8f8}}

